/* App.css */

/* Global Styles */
body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #b3c4a5;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

#root {
  display: flex;
  flex-direction: column;
  flex: 1;
}

/* Navbar Styles */
.navbar {
  background-color: #333;
  color: #fff;
  padding: 1rem;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-brand a {
  color: #fff;
  text-decoration: none;
  font-size: 1.5rem;
}

.navbar-links {
  display: flex;
  gap: 1rem;
}

.navbar-links a {
  color: #fff;
  text-decoration: none;
  padding: 0.5rem 1rem;
}

.navbar-links a:hover {
  background-color: #575757;
}

.hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
}

.hamburger .bar {
  width: 25px;
  height: 3px;
  background-color: #fff;
  margin: 4px;
}

@media (max-width: 768px) {
  .navbar-links {
    display: none;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 60px;
    left: 0;
    background-color: #333;
    text-align: center;
  }

  .navbar-links.open {
    display: flex;
  }

  .hamburger {
    display: flex;
  }
}

/* Hero Section Styles */
.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  margin-top: 4rem; /* To avoid overlap with fixed navbar */
  background-color: #a15d5d;
  flex: 1;
  min-height: 100vh; /* Full screen height */
}

.hero-text {
  flex: 1;
  padding: 1rem;
}

.hero-text h1 {
  font-size: 2.5rem;
}

.hero-image {
  flex: 1;
  padding: 1rem;
}

.hero-image img {
  max-width: 100%;
  height: auto;
}

/* Features Section Styles */
.features {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  background-color: #eef;
  min-height: 100vh; /* Full screen height */
}

.features h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.features p {
  font-size: 1.2rem;
  text-align: center;
  max-width: 800px;
  margin-bottom: 1rem;
}

/* Expense Form Styles */
.expense-form {
  background-color: #fff;
  padding: 2rem;
  margin: 2rem;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  min-height: calc(100vh - 8rem); /* Full screen height with margins */
}

.expense-form input,
.expense-form select,
.expense-form button {
  display: block;
  width: 100%;
  padding: 0.75rem;
  margin: 0.5rem 0;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.expense-form button {
  background-color: #333;
  color: #fff;
  cursor: pointer;
}

.expense-form button:disabled {
  background-color: #ccc;
}

/* Expense List Styles */
.expense-list {
  background-color: #fff;
  padding: 2rem;
  margin: 2rem;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  min-height: calc(100vh - 8rem); /* Full screen height with margins */
}

.expense-list h2 {
  margin-bottom: 1rem;
}

.expense-list ul {
  list-style-type: none;
  padding: 0;
}

.expense-list li {
  padding: 1rem;
  border-bottom: 1px solid #ccc;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.expense-list li:last-child {
  border-bottom: none;
}

.expense-list button {
  background-color: #e74c3c;
  color: #fff;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
}

/* Footer Styles */
.footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 1rem;
  margin-top: auto;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .hero {
    flex-direction: column;
  }
}
